.edit-invoice-btn-main{
    border-radius: 10px;
    padding: 0px !important;
}
.editinvoicebtnmain{
    width: 100%;
    /* padding: 0px !important; */
    height: 120px;
    border-radius: 10px;
    line-height: 120px;
    background-color: #fff;
    text-align: right;
    /* box-shadow: 3px 4px 3px 0px grey !important; */
}
.editinvoicebtnmain .editinvoicebtninner{
    display: inline-block;
}
.editinvoicefrstbtn,.editinvoicescndbtn,.editinvoicethirdbtn{
    height: 45px;
    font-size: 25px;
    line-height: 45px;
    color:#fff;
    border: none;
    border-radius: 12px;
    /* margin: 25px 15px 15px; */
    margin: 0px 15px 15px;
    text-align: center;
}
.editinvoicefrstbtn{
    width: 150px;
    color: #4c4c53;
    background-color: #fff;
    border: 1px solid #000;
}
.editinvoicescndbtn{
    width: 150px;
    background: linear-gradient(to right,#c5d829,#93c73b);
}
.editinvoicethirdbtn{
    width: 150px;
    background-color: #4848d4;
}
.editinvoicefrstbtn:focus,.editinvoicescndbtn:focus,.editinvoicethirdbtn:focus{
    box-shadow: none;
    outline: none;
}
