}
.add-new-invoice-body-logo-btn{
    width: 200px;
    border: 1px solid #F4F5F8;
    height: 30px;
    border-radius: 20px;
    line-height: 15px;
    color: #A4A4A4;
    font-size: 13px;
    text-align: left;
}
.add-new-invoice-body-logo-btn:hover{
    color: #A4A4A4;
}
