    border: none;
    border-radius: 30px;
    margin: 0 50px 0 0;
}
.add-new-invoice-body-client-info-input::placeholder{
    text-align: left;
    color: #a4a4a4;
}
.add-new-invoice-body-client-info-input-square-box{
    height: 30px;
    width: 100%;
