.dashboard-view-green-bg{
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    background-color: #41BEA0;
}
.dashboard-view-bg{
    height: 100vh;
    min-height: 100vh;
    background-color: #ffffff;
    border-radius: 30px 0 0 30px;
}
@media screen and (max-width:1200px){
    .dashboard-view-margin-top-1200px{
        margin: 20px 0 0 0;
    }
}
@media screen and (max-width:767px){
    .dashboard-view-margin-top-767px{
        margin: 20px 0 0 0;
    }
    .dashboard-view-bg{
        border-radius: 0;
    }
}
@media screen and (max-width:850px){
    .dashboard-view-bg{
        border-radius: 0;
    }
}