.invoice-detail-main{
    background-color: #fff;
    /* box-shadow: 3px 4px 3px 0px grey !important; */
    /* padding: 2% 5% 0px !important; */
}
.invoice-head{
    font-size: 20px;
    font-family: Lato;
    margin: 10px 0 40px 25px;
    float: left;
}
.header-text img{
    float: right;
}
.invoice-detail-span p{
    font-size: 20px;
    height: 50px;
    width: 350px;
}
.Invoice-Payments p{
    font-size: 18px;
    height: 50px;
    width: 50%;
    margin-top: 20px;
    float: left;
}
.main-invoice-bar,.main-invoice-second-bar{
    height: 30px;
    width: 100%;
    float: left;
    margin-top: 15px;
    margin-bottom: 55px;
    border-radius: 18px;
    background-color: #f5f5f5;
}
.inner-invoice-bar{
    width: 25%;
    height: 30px;
    border-radius:18px;
    background: linear-gradient(to right,#c5d829,#93c73b);
}
.inner-invoice-second-bar{
    width: 75%;
    height: 30px;
    border-radius: 18px;
    background: linear-gradient(to right,#6abc71,#51c7d7);
}