.settting-body-head-container{
    background-color: #F6F6F6;
    padding: 10px 0 10px 0;
}
.settting-body-head-box{
    border-left: 5px solid #13CE7D;
    height: 50px;
}
.settting-body-head-title{
    line-height: 50px;
    font-size: 18px;
    font-weight: 600;
    color: #13CE7D;
    float: left;
}
.settting-body-head-tab-container{
    float: right;
}
.settting-body-head-tab-box{
    float: left;
    margin: 0 10px;
    line-height: 57px;
    color: #13CE7D;
    font-size: 15px;
    font-weight: 500;
    cursor: pointer;
}
.settting-body-head-tab-box-active{
    border-bottom: 3px solid #D0DD67;
}
.settting-body-head-tab-avatar-container{
    margin: 60px 0 0 0;
}
.settting-body-head-tab-avatar{
    width: 100px;
    height: 100px;
    border: 4px solid #DDF3EE;
    border-radius: 50%;
    z-index: 100;
    position: relative;
}
.settting-body-head-tab-avatar-add-btn{
    background-color: #13CE7D;
    color: #ffffff;
    height: 30px;
    line-height: 15px;
    text-align: right;
    width: 190px;
    z-index: 0;
    position: relative;
    right: 15px;
    top: 20px;
}
.settting-body-head-tab-avatar-add-btn:hover{
    color: #ffffff;
}
.settting-body-head-tab-avatar-add-btn:focus{
    box-shadow: none;
}
.settting-body-head-tab-avatar-add-icon-box{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #29A282;
    position: relative;
    top: -45px;
    left: 80px;
    z-index: 100;
}
.settting-body-head-tab-avatar-add-icon{
    width: 15px;
    height: 15px;
    border-radius: 50%;
    box-shadow: none !important;
    z-index: 100;
    margin: -7px 0 0 3px;
}
.settting-body-head-tab-input-label{
    color: #C3C2C2;
    font-weight: 500;
}
.settting-body-head-tab-input{
    background-color: #F6F6F6;
    border: none;
    border-radius: 5px;
    color: #969B99;
    font-weight: 600;
}
.settting-body-head-tab-input:focus{
    border: 1px solid #f6f6f6;
    background-color: #F6F6F6;
}
.settting-body-head-tab-btn{
    height: 30px;
    line-height: 10px;
    background-color: #13CE7D;
    color: #ffffff;
    border-radius: 20px;
}
.settting-body-head-tab-btn:hover{
    color: #ffffff;
}
.settting-body-head-tab-btn:focus{
    box-shadow: none;
}
.settting-body-head-tab-two-step-title{
    font-size: 20px;
    color: #C3C2C2;
    font-weight: 400;
}
.settting-body-head-tab-two-step-text{
    font-size: 15px;
    color: #C3C2C2;
    text-align: justify;
}