.edit-invoice-container{
    background-color: #fff;
    /* box-shadow: 3px 4px 3px 0px grey !important; */
    border-radius: 10px;
    padding: 2% 0% 0% !important;
}
.edit-invoice-header-text{
    float: left;
    height: 60px;
    border-left: 10px solid #1e6262;
}
.edit-invoice-header-text p{
    font-size: 30px;
    line-height: 60px;
    margin-left: 40px;
}
.edit-invoice-header-delete{
    background: #b80000;    
    color: #fff;
    float: right;
    font-size: 30px;
    width: 65px;
    height: 65px;
    /* padding: 8px; */
    margin-bottom: 15px;
    margin-right: 25px;
    border: none;
    border-radius: 50px;
}
.edit-invoice-header-btn{
    background: linear-gradient(to right,#6abc71,#51c7d7);    
    color: #fff;
    float: right;
    font-size: 15px;
    width: 150px;
    height: 30px;
    margin-bottom: 15px;
    margin-right: 25px;
    border: none;
    border-radius: 8px;
}
.edit-invoice-logo-block{
    width: 55%;
    height: 150px;
    font-size: 25px;
    margin-left: 40px;
    /* text-align: center; */
    line-height: 140px;
    background-color: #e9e9e9;
}
.edit-invoice-text-feild input,.edit-invoice-text-feild2 input{
    width: 80%;
    height: 75px;
    font-size: 18px;
    margin-top: 40px;
    margin-left: 40px;
    color: #000;
    border: 3px solid #e9e9e9;
    border-radius: 12px;
    text-align: center;
}
.edit-invoice-text-feild2 input{
    margin-top: 10px;
}
.edit-invoice-span-text{
    font-size: 30px;
    line-height: 70px;
    margin-left: 75px;
}
.edit-invoice-form-header-text{
    font-size: 35px;
    font-family: Lato;
    margin: 30px;
}
.edit-invoice-form-number input,.edit-invoice-form-date input,.edit-invoice-form-time input{
    height: 60px;
    width: 45%;
    font-size: 25px;
    font-family: lato;
    border-radius: 12px;
    text-align: center;
    margin: 20px;
    border: 3px solid #e9e9e9;
}
.edit-invoice-form-date span,.edit-invoice-form-time span{
    font-size: 20px;
    font-weight: bold;
    font-family: lato;
    margin-left: 20px;
}
.edit-invoice-tble-head{
    font-size: 22px;
    font-weight: bold;
    font-family: Lato;
}
.edit-invoice-tbl-description{
    width: 30%;
}
tr td input{
    width: 100%;
    height: 50px;
    font-size: 18px;
    color: #000;
    padding-left: 10px;
    border: 2px solid #e9e9e9;
    border-radius: 12px;
}
.edit-invoice-total-main{
    margin-top: 50px;
    width:100%;
    text-align: right;
    margin-bottom: 50px;
}
.edit-invoice-total-head,.edit-invoice-total-text{
    display: inline-block;
    line-height: 125px;
}
.edit-invoice-total-text,.edit-invoice-total-text1{
    font-size: 22px;
    margin-right: 50px;
}
.edit-invoice-total-amount,.edit-invoice-total-amount1{
    font-size: 22px;
    /* margin-bottom: -30px; */
    margin-right: 160px;
    color: #e9e9e9;
}
.edit-invoice-note-text{
    font-size: 18px;
    font-family: Lato;
    padding: 20px;
}
.edit-invoice-note-text-field input{
    width: 96%;
    height: 70px;
    /* margin-left: 10px; */
    padding-left: 10px;
    margin-bottom: 70px;
    font-size: 22px;
    border: 3px solid #e9e9e9;
    border-radius: 12px;
}
input:focus,.edit-invoice-header-delete:focus,.edit-invoice-header-btn:focus{
    outline: none;
    box-shadow: none;
}
