.bill-form-main{
    border-radius: 10px;
    background-color: #fff;
    /* box-shadow: 3px 4px 3px 0px grey !important; */
}
.bill-form-header{
    float: left;
    height: 60px;
    border-left: 10px solid #1e6262;
}
.bill-form-header p{
    font-size: 30px;
    line-height: 60px;
    margin-left: 40px;
}
.bill-form-header-btn{
    background: linear-gradient(to right,#6abc71,#51c7d7);    
    color: #fff;
    float: right;
    font-size: 15px;
    width: 150px;
    height: 30px;
    margin-bottom: 15px;
    margin-right: 25px;
    border: none;
    border-radius: 50px;
}
.bill-form-tbl-head-row-height{
    height: 35px;
    color: grey;
}
.bill-form-tbl-body-row-height{
    height: 100px;
    line-height: 75px;
}
.billformmanagehw{
    height: 35px;
    width: 125px;
    margin-top: 23px;
    line-height: 35px;
    text-align: center;
}
.bill-form-supplier-text{
    text-align: center;
    line-height: 20px;
    color: #2d767f;
}
.btn img{
    height: 32px;
    width: 32px;
    padding:7px;
    /* margin-right: 20px; */
    cursor: pointer;
    border-radius: 100px;
    box-shadow: 3px 4px 3px 0px grey;
}
.btn-group .dropdown-menu{
    border-radius: 12px;
    box-shadow: 3px 4px 3px 0px grey;
}
.btn-group .dropdown-menu .dropdown-item{
    font-size: 25px;
    font-weight: bold;
    color: #2d767f;
    text-align: center;
    line-height: 30px;
}
.dropdown-toggle::after { 
    content: none; 
}
.btn-group .btn:focus,.bill-form-header-btn:focus{
    box-shadow: none;
    outline: none;
}

.bill-form-add-bill-modal-close-box{
    width: 30px;
    height: 30px;
    background-color: red;
    border-radius: 50%;
    position: absolute;
    right: -10px;
    top: -10px;
    color: #ffff;
    text-align: center;
    line-height: 30px;
    cursor: pointer;
}
@media only screen and(max-width:992px){
    .bill-form-header p{margin-left: 0px;}
}