.common-view-sidebar-open{
    width: 15%;
    float: left;
}
.common-view-body-open{
    width: 85%;
    float: left;
}
.common-view-sidebar-close{
    width: 6%;
    float: left;
    z-index: 100;
}
.common-view-sidebar-title{
    display: none;
}
.common-view-body-close{
    width: 94%;
    float: left;
    z-index: -100;
}
.cursor-pointer{
    cursor: pointer;
}
.common-docs-icon{
    width: 30px;
    height: 30px;
    cursor: pointer;
    margin: 5px 0 0 0;
}
@media screen and (max-width:1300px){
    .common-view-sidebar-open{
        width: 15%;
    }
    .common-view-body-open{
        width: 85%;
    }
}
@media screen and (max-width:1200px){
    .common-view-sidebar-open{
        width: 16%;
    }
    .common-view-body-open{
        width: 84%;
    }
    .scroll-X{
        overflow-x: scroll;
    }
}
@media screen and (max-width:1100px){
    .common-view-sidebar-open{
        width: 17%;
    }
    .common-view-body-open{
        width: 83%;
    }
}
@media screen and (max-width:1000px){
    .common-view-sidebar-open{
        width: 18%;
    }
    .common-view-body-open{
        width: 82%;
    }
    .common-view-sidebar-close{
        width: 6%;
    }
    .common-view-body-close{
        width: 94%;
    }
}
@media screen and (max-width:900px){
    .common-view-sidebar-open{
        width: 21%;
    }
    .common-view-body-open{
        width: 79%;
    }
    .common-view-sidebar-close{
        width: 7%;
    }
    .common-view-body-close{
        width: 93%;
    }
}
@media screen and (max-width:850px){
    .common-view-sidebar-open{
        width: 0;
    }
    .common-view-body-open{
        width: 100%;
    }
    .common-view-sidebar-close{
        width: 10%;
    }
    .common-view-body-close{
        width: 90%;
    }
}
@media screen and (max-width:767px){
    .common-view-sidebar-open{
        width: 0;
        display: none;
    }
    .common-view-body-open{
        width: 100%;
    }
    .common-view-sidebar-close{
        width: 200px;
        position: absolute;
        z-index: +100;
        display: block !important;
    }
    .common-view-sidebar-title{
        text-align: center;
        display: block;
    }
    .common-view-body-close{
        width: 100%;
    }
}
.common-view-body-scroll{
    height: 100vh;
    width: 100% !important;
    padding: 0;
    overflow-x: hidden;
    overflow-y: scroll;
}