.bill-view-green-bg{
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    background-color: #41BEA0;
}
.bill-view-bg{
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    background-color: #ffffff;
    border-radius: 30px 0 0 30px;
}
@media screen and (max-width:767px){
    .bill-view-bg{
        border-radius: 0;
    }
}