.nav-bg{
    background-color: #ffffff;
    border-radius: 30px 0 0 0;
    z-index: -100;
    padding: 0 0 15px 0;
}
.nav-menu-icon{
    height: 20px;
    width: 20px;
    float: left;
    margin: 25px 0 0 25px;
    cursor: pointer;
}
.nav-invoice-btn{
    border-radius: 20px;
    color: #ffffff;
    background-color: #41BEA0;
    height: 30px;
    line-height: 10px;
    float: right;
    margin: 20px 0 0 0;
}
.nav-invoice-btn:hover{
    color: #ffffff;
}
.nav-invoice-btn:focus{
    box-shadow: none;
}
.nav-action-container{
    width: 150px;
    height: 30px;
    float: right;
    margin: 20px 0 0 0;
}
.nav-action-icon{
    height: 25px;
    width: 25px;
    cursor: pointer;
}
.nav-logout{
    border: 1px solid #13CE7D;
    background-color: #ffffff;
    width: 80px;
    position: absolute;
    right: 0px;
    /* margin: 2px 0 0 0; */
    color: #13CE7D;
    border-radius: 5px;
    display: none;
    cursor: pointer;
}
#nav-profile-icon:hover + .nav-logout{
    display: block;
}
.nav-logout:hover{
    display: block;
    background-color: #13CE7D;
    color: #ffffff;
}
@media screen and (max-width:767px){
    .nav-bg{
        border-radius: 0;
    }
}