.sidebar-bg{
    height: 100vh;
    min-height: 100vh;
    background-color: #41BEA0;
    z-index: 100;
}
.sidebar-option{
    cursor: pointer;
    padding: 7px 0 7px 0;
}
.sidebar-option-active{
    background-color: #54C5AA;
    padding: 7px 0 7px 0;
}
.sidebar-option-text{
    line-height: 20px;
    color: #ffffff;
    text-align: left;
    font-weight: 300;
}
.sidebar-sub-option-text{
    font-size: 12px;
    line-height: 25px;
    color: #ffffff;
    text-align: left;
    font-weight: 300;
}
.sidebar-sub-option-active{
    border-left: 3px solid #ACD032;
    margin: 0 0 0 -3px;
}
.sidebar-option-icon{
    float: left;
    height: 20px;
    width: 20px;
    margin: 0 10px 0 0;
}
.sidebar-close{
    display: none;
    width: 15px;
    height: 15px;
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
}
@media screen and (max-width:767px){
    .sidebar-close{
        display: block;
    }
    .sidebar-option-icon{
        margin: 0 0 0 15px;
    }
}