.invoice-view-green-bg{
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    background-color: #41BEA0;
  }
  .invoice-view-bg{
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    background-color: #ffffff;
    border-radius: 30px 0 0 30px;
  }
  @media screen and (max-width:767px){
    .invoice-view-bg{
        border-radius: 0;
    }
    .invoice-view-btn-margin{
      margin: 100px 0 0 0 !important;
    }
  }