.dashboard-nav-menu-icon{
    height: 20px;
    width: 20px;
    float: left;
    margin: 25px 0 0 0;
    cursor: pointer;
}
.dashboard-nav-invoice-btn{
    border-radius: 20px;
    color: #ffffff;
    background-color: #13CE7D;
    height: 30px;
    line-height: 10px;
    float: right;
    margin: 20px 0 0 0;
}
.dashboard-nav-invoice-btn:hover{
    color: #ffffff;
}
.dashboard-nav-invoice-btn:focus{
    box-shadow: none;
}
.dashboard-nav-action-container{
    width: 150px;
    height: 30px;
    float: right;
    margin: 20px 0 0 0;
}
.dashboard-nav-action-icon{
    height: 25px;
    width: 25px;
    cursor: pointer;
}
.dashboard-line-chart-title{
    font-size: 18px;
    color: #000000;
    font-weight: 600;
    text-align: left;
}
.dashboard-line-chart-dropdown-btn{
    color: #BBBBBB;
    font-size: 12px;
}
.dashboard-line-chart-dropdown-btn:focus{
    box-shadow: none;
}
.dashboard-line-chart-dropdown-box{
    border: none;
}
.dashboard-line-chart-amount{
    font-size: 20px;
    font-weight: 600;
    color: #71C898;
    text-align: left;
}
.dashboard-line-chart-interval{
    font-size: 12px;
    font-weight: 500;
    color: #BBBBBB;
    text-align: left;
}
.dashboard-invoice-count-invoice-container{
    width: 60px;
    height: 60px;
    /* background-color: pink; */
    position: relative;
    top: -110px;
    left: 45px;
}
.dashboard-invoice-progress-bar-days{
    font-size: 13px;
    font-weight: 500;
    color: #0a0a0a;
    line-height: 250%;
    text-align: left;
}
.dashboard-invoice-progress-bar-amount{
    color: #0a0a0a;
    font-size: 13px;
    font-weight: 500;
}
.dashboard-invoice-progress-bar-amount-label{
    color: #0a0a0a;
    font-size: 13px;
    font-weight: 400;
}
.dashboard-invoice-progress-bar-bar-top{
    background-image: linear-gradient(to right,rgb(248, 248, 81),rgb(137, 219, 137));
    border-radius:10px
}
.dashboard-invoice-progress-bar-bar-bottom{
    background-image: linear-gradient(to right,rgb(248, 248, 81),rgb(137, 219, 137),rgb(101, 101, 228));
    border-radius:10px
}
.dashboard-profit-loss-amount{
    font-size: 10px;
}
.dashboard-profit-loss-amount-label{
    font-size: 10px;
    color: #BBBBBB;
}