.invoicebtn-pdng{
    padding: 0px !important;
    box-shadow: none !important;
}
.btnmain{
    width: 100%;
    height: 100px;
    text-align: left;
}
.btnmain .btninner{
    display: inline-block;
}
.frstbtn,.scndbtn,.thirdbtn{
    width: 125px;
    height: 40px;
    font-size: 22px;
    line-height: 40px;
    color:#fff;
    border: none;
    margin: 25px 15px 15px;
    text-align: center;
}
.frstbtn{
    background: linear-gradient(to right,#c5d829,#93c73b);
}
.scndbtn{
    background-color: #f53c56;
}
.thirdbtn{
    width: 225px;
    background-color: #2dce98;
}
.frstbtn:focus,.scndbtn:focus,.thirdbtn:focus{
    box-shadow: none;
    outline: none;
}

.invoice-body-btn{
    height: 30ox;
    width: 90px;
    font-size: 12px;
    line-height: 10px;
    border-radius: 20px;
    color: #ffff;
    margin: 5px 10px 5px 5px;
    float: left;
    text-align: center;
}
.invoice-body-btn:hover{
    color: #ffffff;
}
.invoice-body-btn:focus{
    box-shadow: none;
}
.invoice-body-all-btn{
    background-color: #C8DB2A;
}
.invoice-body-overdue-btn{
    background-color: #F53C56;
}
.invoice-body-due-btn{
    background-color: #41BEA0;
}
.invoice-body-draft-btn{
    background-color: #303A52;
}