.addexpense-span-text-border {
  height: 75px;
  border-left: 10px solid #1e6262;
}
.addExpense-span-text {
  line-height: 75px;
}
.addExpense-span-text,
.addExpanse-table-text {
  color: #1e6262;
  font-size: 30px;
}
.addExpense-text-feild {
  height: 75px;
  font-size: 25px;
  font-weight: bold;
  color: #9b9b9b;
  border: 1px solid #000;
  border-radius: 10px;
}
.addExpense-text-feild:focus,
.addExpense-btn:focus {
  outline: none;
  box-shadow: none;
}
.addExpense-btn {
  background: linear-gradient(to right, #c5d829, #93c73b);
  color: #fff;
  font-size: 20px;
  width: 225px;
  height: 65px;
  border: none;
  border-radius: 50px;
}
.main {
  position: relative;
}
.main img {
  height: 20px;
  width: 20px;
  /* background-color: #f7fafc; */
  cursor: pointer;
  /* border-radius: 100px;
  box-shadow: 3px 3px 3px 0px #f7fafc; */
}
.main .hover-div {
  background-color: #fff;
  height: 130px;
  width: 150px;
  border: 1px solid grey;
  font-size: 25px;
  font-weight: bold;
  line-height: 40px;
  float: left;
  position: absolute;
  z-index: 100;
  top: 75px;
  left: -100px;
  color: #2d767f;
  border-radius: 10px;
  display: none;
}
.main .hover-div div:hover {
  background-color: #9b9b9b;
  cursor: pointer;
}
