.bill-navigation {
  float: right;
}
.bill-navigation p {
  border: 1px solid gray;
  width: 45px;
  text-align: center;
  cursor: pointer;
  float: left;
  height: 45px;
  padding: 12px;
  border-radius: 50%;
}
.bill-navigation p:hover {
  background-color: grey;
  color: #fff;
}
.bill-navigation p {
  margin-bottom: 10px;
}
