
.qutation-table-body-Information-Heading-Row{
    font-size: 12px;
    background-color: #F1F3F9;
    color: #9698BC;  
}
.qutation-table-body-heading-text{
   margin-right: 3px;
   vertical-align: top;

}
.qutation-table-body-heading-arrow-icon{
    font-size: 9px;
}
.qutation-table-body-text-head-row{
    color: #172B4D;
    font-size: 13px;
    /* background-color: #F7FAFC; */
}
.qutation-table-body-text-head-row:hover{
   background-color: #F7FAFC;
}
.qutation-table-ellipsis-dropdown-icon{
    background-color: white;
    border-radius: 50%;
    height: 30px;
    width: 30px;
}
.qutation-table-Action-dropdown{
  border-radius: 8px;
}

.qutation-qotation-button{
    border: none;
    background-color: #2DCE98;
    color: white;
    width: 110px;
    height: 25px;
    font-size: 12px;
    line-height: 5px;
}
.qutation-qotation-button:focus{
    outline: none;
}
.qutation-list-table{
    width: 100%;
}
.quotation-side-border{
    border-left: 5px solid #1E6262;
}
.Table-Qutation{
    font-size: 20px;
    font-weight: 600;
    line-height: 50px;
    color: #1E6262;
}
.qutation-get-quotation-btn{
    color: #ffffff;
    height: 30px;
    line-height: 10px;
    border-radius: 20px;
    margin: -20px 0 0 0;
    background-color: #41BEA0;
    /* background-image: linear-gradient(to right,#74C362,#56C5C7); */
}
.qutation-get-quotation-btn:hover{
    color: #ffffff;
}
.qutation-get-quotation-btn:focus{
    box-shadow: none;
}
.qutation-table-option-icon{
    height: 15px;
    width: 15px;
    margin: 5px 0 0 7px;
}
@media only screen and (max-width:700px){
    .table-search-image{
        width: 20px;
    }
}
