.invoices-main{
    background-color: #fff;
    /* box-shadow: 3px 4px 3px 0px grey !important; */
}
.invoice-header{
    float: left;
    height: 60px;
    border-left: 10px solid #1e6262;
}
.invoice-header p{
    font-size: 30px;
    line-height: 60px;
    margin-left: 35px;
}
.invoice-header-btn{
    /* background: linear-gradient(to right,#6abc71,#51c7d7);     */
    background-color: #41BEA0;
    color: #fff;
    float: right;
    font-size: 15px;
    width: 140px;
    height: 30px;
    border: none;
    margin-right: 20px;
    margin-bottom: 20px;
    border-radius: 50px;
}
.head-row-height{
    height: 35px;
    color: grey;
}
.body-row-height{
    height: 75px;
    line-height: 75px;
}
.managehwbrdr{
    margin-top: 25px;
    line-height: 30px;
    height: 30px;
    width: 150px;
    border-radius: 20px;
    text-align: center;
}
.managehw{
    height: 30px;
    width: 125px;
    margin-top: 25px;
    line-height: 30px;
    text-align: center;
}
.btn img{
    height: 32px;
    width: 32px;
    padding:7px;
    /* margin-right: 20px; */
    cursor: pointer;
    border-radius: 100px;
    /* box-shadow: 3px 4px 3px 0px grey; */
}
.btn-group .dropdown-menu{
    border-radius: 12px;
    /* box-shadow: 3px 4px 3px 0px grey; */
}
.btn-group .dropdown-menu .dropdown-item{
    font-size: 25px;
    font-weight: bold;
    color: #2d767f;
    text-align: center;
    line-height: 30px;
}
.dropdown-toggle::after { 
    content: none; 
}
.invoice-header-btn:focus,.btn-group .btn{
    box-shadow: none;
    outline: none;
}
