.invoices-badge{
    width: 120px;
    height: 30px;
    border-radius: 20px;
    color: #ffffff;
    margin: 25px 0 0 0;
    line-height: 30px;
}
.invoices-badge-overdue{
    background-color: #F53C56;
}
.invoices-badge-draft{
    background-color: #303A52;
}
.invoices-badge-due{
    background-color: #41BEA0;
}