.profit-loss-body-head-title{
    float: left;
    color: #1E6262;
    font-size: 20px;
    font-weight: 500;
}
.profit-loss-body-head-down-icon{
    height: 15px;
    width: 15px;
    float: right;
}
.profit-loss-body-net-profit-amount{
    color: #1E6262;
    font-size: 22px;
    font-weight: 500;
}
.profit-loss-body-net-profit-text{
    color: rgb(189, 189, 189);
}
.profit-loss-body-income-box{
    border-left: 4px solid #00E295;
}
.profit-loss-body-income-amount{
    font-size: 15px;
    color: #1E6262;
    font-weight: 500;
}
.profit-loss-body-income-text{
    color: rgb(189, 189, 189);
}
.profit-loss-body-expense-box{
    border-left: 4px solid #00ABCD;
}
.profit-loss-body-expense-amount{
    font-size: 15px;
    color: #1E6262;
    font-weight: 500;
}
.profit-loss-body-expense-text{
    color: rgb(189, 189, 189);
}
.profit-loss-body-statement-head{
    border-left: 5px solid #1E6262;
}
.profit-loss-body-statement-head-title{
    float: left;
    color: #1E6262;
    font-size: 20px;
    font-weight: 500;
    line-height: 40px;
}
.profit-loss-body-statement-head-add-btn{
    float: right;
    height: 30px;
    border-radius: 20px;
    line-height: 15px;
    color: #ffffff;
    background-color: #41BEA0;
}
.profit-loss-body-statement-head-add-icon{
    box-shadow: none !important;
    width: 15px;
    height: 15px;
    margin: -7px 0 0 0;
    float: left;
}
.profit-loss-body-statement-head-add-btn:hover{
    color: #ffffff;
}
.profit-loss-body-statement-head-add-btn:focus{
    box-shadow: none;
}
.profit-loss-body-statement-year-box{
    background-color: #F1F3F9;
}
.profit-loss-body-statement-table-title{
    font-size: 15px;
    font-weight: 500;
    color: #6FA6A6;
    line-height: 30px;
    padding: 0 0 0 50px;
}
.profit-loss-body-statement-table-sub-title{
    font-size: 14px;
    font-weight: 500;
    color: #6FA6A6;
    line-height: 30px;
    padding: 0 0 0 70px;
}
.profit-loss-body-statement-table-amount{
    font-size: 15px;
    font-weight: 500;
    color: #A7A7A7;
    line-height: 30px;
    text-align: right;
}
.profit-loss-body-statement-table-stripped-box{
    background-color: #F7FAFC;
}
.profit-loss-body-statement-modal-add-btn{
    background-color: #41BEA0;
    color: #ffffff;
    height: 30px;
    line-height: 15px;
}
.profit-loss-body-statement-modal-add-btn:hover{
    color: #ffffff;
}
@media only screen and (max-width:767px){
    .profit-loss-body-statement-table-title{
        padding: 0 0 0 10px;
    }
    .profit-loss-body-statement-table-sub-title{
        padding: 0 0 0 20px;
    }
}